* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

header {
  height: 100vh;
  background-image: url("ng.2021b2a5.jpg");
  background-size: cover;
}

header h1 {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .3em;
  margin-left: 30px;
}

.titulo {
  width: 100%;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 40vh 0 0;
  display: flex;
}

.comenzar {
  margin-top: 5vh;
}

.comenzar a {
  text-decoration: none;
}

.sonido {
  padding: 10%;
  display: block;
  overflow: auto;
}

.contenido-sonido {
  text-align: center;
}

.contenido-sonido h2, p {
  margin: .5em;
}

.separador {
  margin: 5em;
}

.acerca {
  height: 100vh;
  background-color: #d1d1d1;
  place-items: center;
  padding: 10%;
  display: grid;
  overflow: auto;
}

.acerca h2 {
  text-transform: uppercase;
}

.acerca p {
  text-align: justify;
  color: #000;
  margin: 2em;
}

.contacto {
  height: 100vh;
  padding: 10%;
}

footer {
  height: 10vh;
  color: #fff;
  background-color: #000;
  place-items: center;
  display: grid;
}

.page-wrapper {
  width: 100%;
  height: 100%;
}

.nav-wrapper {
  width: 100%;
  background-color: #fff;
  position: sticky;
  top: 0;
}

.navbar {
  height: 50px;
  grid-template-columns: 1fr 3fr;
  align-items: center;
  display: grid;
  overflow: hidden;
}

.nav-title h1 {
  color: #000;
}

.navbar ul {
  grid-template-columns: repeat(4, 1fr);
  justify-self: end;
  margin-right: 1vw;
  list-style: none;
  display: grid;
}

.nav-item a {
  color: #000;
  text-transform: uppercase;
  letter-spacing: .1rem;
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s ease-out;
}

.nav-item a:hover {
  color: #5c5c5c;
}

.features {
  width: 100%;
  height: auto;
  background-color: #f1f1f1;
  justify-content: space-around;
  padding: 50px 20px;
  display: flex;
}

.feature-container {
  flex-basis: 30%;
  margin-top: 10px;
}

.feature-container p {
  color: #000;
  text-align: center;
  margin-bottom: 15px;
  line-height: 1.4;
}

.feature-container img {
  width: 100%;
  margin-bottom: 15px;
}

.menu-toggle .bar {
  width: 25px;
  height: 3px;
  -o-transition: all .3s ease-in-out;
  background-color: #3f3f3f;
  margin: 5px auto;
  transition: all .3s ease-in-out;
}

.menu-toggle {
  justify-self: end;
  margin-right: 25px;
  display: none;
}

.menu-toggle:hover {
  cursor: pointer;
}

#mobile-menu.is-active .bar:nth-child(2) {
  opacity: 0;
}

#mobile-menu.is-active .bar:nth-child(1) {
  transform: translateY(8px)rotate(45deg);
}

#mobile-menu.is-active .bar:nth-child(3) {
  transform: translateY(-8px)rotate(-45deg);
}

@keyframes gradbar {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

@media only screen and (max-width: 720px) {
  .features {
    flex-direction: column;
    padding: 50px;
  }

  h1 {
    font-size: 1.9rem;
  }

  h2 {
    font-size: 1rem;
  }

  p {
    font-size: .8rem;
  }

  .navbar ul {
    width: 100%;
    height: calc(100vh - 55px);
    text-align: center;
    background-color: #fff;
    flex-direction: column;
    justify-content: start;
    display: flex;
    position: fixed;
    top: 55px;
    overflow: hidden;
    transform: translate(-101%);
  }

  .navbar li {
    padding: 15px;
  }

  .navbar li:first-child {
    margin-top: 50px;
  }

  .navbar li a {
    font-size: 1rem;
  }

  .menu-toggle, .bar {
    cursor: pointer;
    display: block;
  }

  .mobile-nav {
    transform: translate(0%) !important;
  }

  .headline {
    height: 20vh;
  }

  .feature-container p {
    margin-bottom: 25px;
  }

  .feature-container {
    margin-top: 20px;
  }

  .feature-container:nth-child(2) {
    order: -1;
  }
}

.arrow {
  opacity: 0;
  transform-origin: 50%;
  margin-top: 45vh;
  position: absolute;
  top: 50%;
  left: 50%;
}

.arrow-first {
  animation: 2s ease-in-out infinite arrow-movement;
}

.arrow-second {
  animation: 2s ease-in-out 1s infinite arrow-movement;
}

.arrow:before, .arrow:after {
  content: "";
  height: 3px;
  width: 30px;
  background: #fff;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.arrow:before {
  transform-origin: 0 0;
  transform: rotate(45deg)translateX(-23%);
}

.arrow:after {
  transform-origin: 100% 0;
  transform: rotate(-45deg)translateX(23%);
}

@keyframes arrow-movement {
  0% {
    opacity: 0;
    top: 45%;
  }

  70% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

#button-top {
  width: 50px;
  height: 50px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  -o-transition: background-color .3s, opacity .5s, visibility .5s;
  background-color: #000;
  border-radius: 100px;
  transition: background-color .3s, opacity .5s, visibility .5s;
  display: inline-block;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

#button-top:after {
  content: "";
  color: #fff;
  font-family: FontAwesome;
  font-size: 1.5em;
  font-style: normal;
  font-weight: normal;
  line-height: 45px;
}

#button-top:hover {
  cursor: pointer;
  background-color: #333;
}

#button-top:active {
  background-color: #555;
}

#button-top.show {
  opacity: 1;
  visibility: visible;
}

